/* STATIC STYLES (desktop-first) */

.portfolio-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  gap: 2rem;
}

.portfolio-card-container {
  position: relative;

  margin-top: 4rem;
}

.portfolio-card {
  position: relative;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 2rem;

  text-align: center;

  gap: 1rem;
}

.portfolio-card-tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 1rem;
}

.portfolio-card-tags img {
  width: 1rem;
  opacity: 0.7;
}

.laptop-container {
  width: 50vw;
  height: 70vh;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .laptop-container {
    height: 55vh;
  }
}

@media (max-width: 768px) {
  .portfolio-content {
    grid-template-columns: 1fr;

    gap: 0;
  }

  .portfolio-card-container {
    margin-top: 0;
  }

  .laptop-container {
    width: 90vw;
    height: 65vh;
  }
}

@media (max-width: 576px) {
  .laptop-container {
    width: 90vw;
    height: 40vh;
  }
}
