/* STATIC STYLES (desktop-first) */

.experience-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.developer-container {
  position: relative;

  height: 100%;
}

.experience-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: start;

  gap: 2rem;
}

.experience-cards > * {
  flex: 0 0 16rem;
}

.experience-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 2rem 3rem;

  cursor: pointer;

  gap: 1rem;
}

.experience-card-inner p {
  display: flex;
  align-items: center;

  margin-bottom: 0.5rem;

  gap: 0.5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .experience-card {
    padding: 1.5rem 2rem;
  }

  .icon-card-content {
    padding: 1.2rem;
  }
}

@media (max-width: 768px) {
  .experience-icons > * {
    flex: 0 0 10rem;
  }
}

@media (max-width: 576px) {
  .experience-content {
    display: flex;
    flex-wrap: wrap;

    gap: 2rem;
  }

  .developer-container {
    flex: 1;

    height: 40vh;
    margin-top: -2rem;
  }

  .experience-cards {
    flex: 2;
  }

  .icon-card-content {
    padding: 1rem;
  }
}
