/* STATIC STYLES (desktop-first) */

.navbar {
  position: fixed;
  top: 0;
  z-index: 99999;

  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  margin: 0 auto;
  padding: 1rem 1.5rem;

  background-color: transparent;

  transition: background-color 0.3s ease;
}

.navbar.scrolled {
  background-color: #050816;
}

.logo-container {
  display: flex;
  align-items: center;

  text-decoration: none;

  gap: 1rem;
}

.logo {
  width: 2.5rem;

  object-fit: contain;
}

.logo-text {
  display: flex;

  cursor: pointer;
}

.desktop-links {
  display: flex;
  flex-direction: row;

  list-style: none;

  gap: 1.5rem;
}

.desktop-link {
  color: var(--color-primary-text-dark);

  transition: color 0.3s ease;
  cursor: pointer;
}

.desktop-link:hover {
  color: var(--color-primary-text);
}

.desktop-link.active {
  color: var(--color-primary-text);
}

.desktop-link a {
  position: relative;

  place-items: center;

  text-decoration: none;
  color: inherit;
}

.underline {
  position: absolute;
  bottom: -0.5rem;
  left: 50%;

  width: 0;
  height: 0.125rem;

  background-color: var(--color-primary-text);

  transform: translateX(-50%);
  transition: all 0.3s ease;
}

.desktop-link:hover .underline {
  width: 100%;
}

.desktop-link.active .underline {
  width: 100%;
}

.mobile-menu {
  display: none;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
}

.menu-icon {
  width: 28px;
  height: 28px;

  cursor: pointer;

  object-fit: contain;
}

.mobile-links {
  position: absolute;
  top: 80px;
  right: 20px;
  z-index: 10;

  display: none;

  min-width: 140px;
  padding: 20px;

  border-radius: 10px;

  background: linear-gradient(140deg, #fff -300%, #6d6d6d -70%, #11101d 100%);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.7);
}

.mobile-links.show {
  display: flex;
}

.mobile-links ul {
  display: flex;
  flex-direction: column;

  width: 100%;

  list-style: none;

  gap: 1rem;
}

.mobile-link-item {
  color: var(--color-primary-text-dark);

  transition: color 0.3s ease;
  cursor: pointer;
}

.mobile-link-item:hover {
  color: var(--color-primary-text);
}

.mobile-link-item.active {
  color: var(--color-primary-text);
}

.mobile-link-item a {
  text-decoration: none;
  color: inherit;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .desktop-links {
    display: none;
  }

  .mobile-menu {
    display: flex;
  }
}

