@import './root-styles.css';
@import './root-utils.css';
@import './root-typography.css';
@import './utils.css';

/* STATIC STYLES (desktop-first) */

.app {
  display: flex;
  flex-direction: column;

  min-height: 100vh;
}

main {
  flex: 1;
}

section:not(:first-child) {
  margin: 0 auto;
  padding: 10rem 5rem;
}

.section-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-bottom: 5rem;

  gap: 1rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  section:not(:first-child) {
    padding: 8rem 4rem;
  }
}

@media (max-width: 992px) {
  section:not(:first-child) {
    padding: 6rem 3rem;
  }
}

@media (max-width: 768px) {
  section:not(:first-child) {
    padding: 4rem 2rem;
  }

  .section-header {
    margin-bottom: 3rem;
  }
}

@media (max-width: 576px) {
  section:not(:first-child) {
    padding: 2rem 1rem;
  }

  .section-header {
    margin-bottom: 2rem;
  }
}
