/* STATIC STYLES (desktop-first) */

.services-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 2rem;
}

.services-cards > * {
  flex: 0 0 25rem;
}

.services-card {
  place-items: center;

  padding: 2rem;
}

.services-card h3 {
  margin-bottom: 1rem;

  line-height: 1.1;
  text-align: center;
}

.services-card-elements {
  display: flex;
  flex-direction: column;

  gap: 0.5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .services-cards > * {
    flex: 0 0 20rem;
  }
}

@media (max-width: 768px) {
}

@media (max-width: 576px) {
  .services-cards {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));

    gap: 2rem;
  }

  .services-cards > * {
    flex: 0 0 80vw;
  }
}
