/* STATIC STYLES (desktop-first) */

@property --angle {
  inherits: false;
  initial-value: 0deg;
  syntax: '<angle>';
}

@keyframes spin {
  to {
    --angle: 360deg;
  }
}

.card3 {
  position: relative;

  overflow: hidden;
  border: none;
  border-radius: 1rem;

  background-color: var(--color-card);
  backdrop-filter: blur(10px);

  transition: transform 0.4s cubic-bezier(0.18, 0.89, 0.32, 1.28), box-shadow 0.3s ease, background-color 0.3s ease;

  isolation: isolate;
}

.card3::before {
  content: '';
  position: absolute;
  z-index: -1;

  padding: 2px;

  border-radius: inherit;

  background: linear-gradient(45deg, var(--color-primary), var(--color-secondary), var(--color-primary));

  inset: 0;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.card3::after {
  content: '';
  position: absolute;
  z-index: -1;

  padding: 2px;

  border-radius: inherit;

  background: conic-gradient(from var(--angle), var(--color-primary), var(--color-secondary), var(--color-primary));
  opacity: 0;

  transition: opacity 0.3s ease;

  inset: 0;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.card3:hover {
  background-color: color-mix(in srgb, var(--color-primary-darker), white 2%);
  box-shadow: 0 12px 20px -10px var(--color-primary-darker), 0 4px 20px 0 var(--color-primary-darker);
}

.card3:hover::after {
  opacity: 1;

  animation: spin 1s linear infinite;
}

