/* STATIC STYLES (desktop-first) */

.card1 {
  position: relative;

  overflow: hidden;
  border-radius: 0.5rem;

  background-color: var(--color-primary-darker);

  transition: transform 0.4s cubic-bezier(0.18, 0.89, 0.32, 1.28),
  box-shadow 0.3s ease,
  background-color 0.3s ease;
}

.card1:hover {
  background-color: color-mix(in srgb, var(--color-primary-darker), var(--color-secondary) 2%);
  box-shadow: 0 12px 20px -10px var(--color-primary-darker),
  0 4px 20px 0 var(--color-primary-darker);

  transform: translateY(-8px) scale(1.02);
}

.spotlight-container {
  position: absolute;

  inset: 0;
}

.spotlight-container > img {
  width: 100%;
  height: 100%;

  border-radius: 0.5rem;

  object-fit: fill;
}

