/* STATIC STYLES (desktop-first) */

.about-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 2rem;
}

.about-cards > * {
  flex: 0 0 20rem;
}

.about-card {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 2rem;

  text-align: center;

  gap: 1rem;
}

.technologies {
  display: flex;
  justify-content: center;

  margin-top: 1rem;

  gap: 1rem;
}

