/* STATIC STYLES (desktop-first) */

.card2 {
  position: relative;

  overflow: hidden;
  border: 1px solid var(--color-primary-darkest);
  border-radius: 1rem;

  background-color: var(--color-primary-darker);

  transition: border-color 1s ease-in-out;

  break-inside: avoid-column;
  --start: 0;
}

.card2::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;

  width: 100%;
  height: 100%;

  border: 2px solid transparent;
  border-radius: 1rem;

  background: radial-gradient(circle, var(--color-primary-text) 0%, var(--color-primary) 100%);
  background-attachment: fixed;
  opacity: 0;

  transform: translate(-50%, -50%);
  transition: 0.5s ease;

  mask: linear-gradient(#0000, #0000), conic-gradient(from calc((var(--start) - 15) * 1deg), #ffffff1f 0deg, #fff, #ffffff00 100deg);
  mask-clip: padding-box, border-box;
  mask-composite: intersect;
}

.card2:hover::before {
  opacity: 1;
}

.card2-glow {
  position: absolute;
  top: 50%;
  left: 50%;

  width: 100%;
  height: 100%;

  filter: blur(10px) saturate(200%);

  transform: translate(-50%, -50%);
  pointer-events: none;
}

