/* STATIC STYLES (desktop-first) */

.btn {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 0.5rem;

  overflow: hidden;
  border-radius: 0.5rem;

  background-color: var(--color-primary-dark);

  text-decoration: none;

  transition: transform 0.3s ease;
  cursor: pointer;
}

.btn .circle {
  position: absolute;

  width: 120%;
  height: 120%;

  border-radius: 9999px;

  background-color: var(--color-secondary-dark);

  transform-origin: center;
  transition: all 0.5s ease;
}

.btn:hover .circle {
  right: 1rem;

  width: 2.5rem;
  height: 2.5rem;
}

.btn .text {
  z-index: 1;

  color: var(--color-primary-darkest);

  transform: translateX(-0.5rem);
  transition: all 0.5s ease;
}

.btn:hover .text {
  color: var(--color-primary-text-dark);
}

.btn .icon-wrapper {
  position: absolute;
  top: 50%;
  right: 1rem;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 2.5rem;
  height: 2.5rem;

  overflow: hidden;
  border-radius: 9999px;

  transform: translateY(-50%);
  transition: all 0.5s ease;
}

.btn:hover .icon-wrapper {
  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-50%) translateY(0.15rem);
  }
  50% {
    transform: translateY(-50%) translateY(-0.25rem);
  }
}

