/* STATIC STYLES (desktop-first) */

#hero {
  position: relative;

  display: flex;
  justify-content: space-around;
  align-items: center;

  width: 100vw;
  height: 100vh;

  overflow: hidden;

  padding-block: 5rem;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  opacity: 0.1;
}

.hero-container {
  position: relative;
  z-index: 10;

  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 100%;
  padding: 0 10%;
}

.hero-text-container {
  display: flex;
  flex-direction: column;

  max-width: 50%;

  gap: 2rem;
}

.hero-text-container h1 {
  line-height: 1.2;
  color: var(--color-primary-text);
}

.hero-text-container h3 {
  line-height: 1.2;
  color: var(--color-primary-text-dark);
}

.desk-container {
  flex: 1;

  height: 100%;
  max-width: 45%;
}

.sliding-line {
  display: flex;
  align-items: center;

  line-height: 1.2;
  white-space: nowrap;

  gap: 0.5rem;
}

.slide {
  position: relative;

  display: inline-block;

  height: 2.5rem;

  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;

  animation: wordSlider 6s infinite ease-in-out;
}

.sliding-word {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  height: 2.5rem;
  padding-left: 0.2rem;

  white-space: nowrap;
}

@keyframes wordSlider {
  0%, 25% {
    transform: translateY(0%);
  }
  35%, 65% {
    transform: translateY(-50%);
  }
  75%, 100% {
    transform: translateY(0%);
  }
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .hero-container {
    flex-direction: column;
    justify-content: center;

    padding: 8rem 2rem 0 2rem;
  }

  .hero-text-container {
    place-items: center;

    max-width: 100%;

    text-align: center;

    gap: 1rem;
  }
}

@media (max-width: 768px) {
  .desk-container {
    min-width: 100%;
    margin-top: 3rem;
  }
}

