/* zmienne: https://colorhunt.co/palettes/dark */
:root {
  --color-primary: hsl(240, 10%, 54%);
  --color-primary-dark: hsl(240, 10%, 25%);
  --color-primary-darker: hsl(240, 10%, 10%);
  --color-primary-darkest: hsl(240, 10%, 3%);

  --color-primary-text: hsl(240, 10%, 95%);
  --color-primary-text-dark: hsla(240, 10%, 95%, 0.7);

  --color-secondary: hsl(191, 100%, 40%);
  --color-secondary-dark: hsla(191, 100%, 70%, 0.7);
  --color-secondary-darker: hsla(191, 100%, 70%, 0.4);
  
  --color-blur: hsla(240, 10%, 70%, 0.1);
  --color-card: hsla(240, 10%, 12%, 0.85);
  
  --ff: "Lato", sans-serif;
  --fw: 500;

  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}


*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  outline-color: transparent;
  text-decoration: none;
  list-style: none;
}


html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}


body {
  font-family: var(--ff);
  font-weight: var(--fw);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  background: var(--color-primary-darkest);
  color: var(--color-primary-text);
}


input, textarea, button, select, a {
  -webkit-tap-highlight-color: transparent;
}


img {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}