/* STATIC STYLES (desktop-first) */

.cards-navigation {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-top: 2rem;

  gap: 2rem;
}

.cards-navigation.wide {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  z-index: 100;

  justify-content: space-between;

  padding: 0 1rem;

  transform: translateY(-50%);
}

.cards-navigation-arrow {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 3rem;
  height: 3rem;

  border: none;
  border-radius: 50%;

  background: var(--color-primary-darker);

  color: var(--color-primary-text);

  transition: all 0.3s ease;
  cursor: pointer;
}

.cards-navigation-arrow:hover {
  background: var(--color-primary-dark);

  transform: scale(1.1);
}

.cards-navigation-arrow:active {
  transform: scale(0.95);
}

.cards-navigation-counter {
  display: flex;
  align-items: center;

  color: var(--color-primary-text);

  gap: 0.5rem;
}

