/* STATIC STYLES (desktop-first) */

.contact-cards {
  display: grid;
  grid-template-columns: repeat(2, 20rem);

  width: max-content;
  margin: 0 auto;

  gap: 5rem;
}

.contact-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 2rem;

  gap: 1rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .contact-cards {
    gap: 4rem;
  }
}

@media (max-width: 992px) {
  .contact-cards {
    gap: 3rem;
  }
}

@media (max-width: 768px) {
  .contact-cards {
    grid-template-columns: repeat(1, clamp(10rem, 40vw, 20rem));

    gap: 2rem;
  }
}

@media (max-width: 576px) {
  .contact-cards {
    grid-template-columns: repeat(1, 75vw);
  }
}
